body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
  padding: 30px 20px;
  box-shadow: 0px 0px 5px rgb(46, 37, 37);
}

.header_link {
  display: inline-block;
  padding-left: 20px;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
  color: black;
}

.header_link.active{
  color: #c22e2e;
}

.movies_link{
  text-decoration: none;
  font-size: 20px;
}

.movie_details_container{
  display: flex;
  gap: 40px;
  border-bottom: 2px solid black;
  padding: 25px;
}

.movie_details_desc {
  display: flex;
  flex-direction: column;
}

.cast_wrapper {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.actor_title {
  padding: bottom 0;
  margin: 0;
}

.actor_desc {
  margin: 0;
  padding-bottom: 20px;
}

.go-back-link{
  text-decoration: none;
  font-size: 18px;
  padding: 10px;
  color: black;
}

.movie_search_input{
  width: 300px;
  height: 25px;
  margin-top: 20px;
}

.movie_search_btn{
  width: 100px;
  height: 25px;
  background-color: #fff;
  border-radius: 10px;
  margin-left: 10px;
}